body {
  font-family: 'Roboto Slab', serif !important; }

.error {
  color: red; }

.controller-block {
  overflow: hidden; }

#pinContainer {
  height: 100vh;
  width: 100vw;
  overflow: hidden; }

#pinContainer .panel {
  height: 100vh;
  width: 100vw;
  position: absolute;
  text-align: center; }

.panel span {
  position: relative;
  display: block;
  top: 50%;
  font-size: 80px; }

.panel.blue {
  background-size: cover; }

.panel.turqoise {
  background-color: #38ced7; }

.panel.green {
  background-color: #22d659; }

.panel.blue {
  background-image: url(/portfolio/static/media/Portfolio-BG.c48255e0.jpg); }

.landing-banner {
  display: flex;
  justify-content: center;
  align-items: center; }

.banner-name h1, .banner-name h2 {
  color: #ffffff;
  font-size: 43px;
  line-height: 55px; }

.banner-name p {
  font-size: 23px;
  font-weight: 600;
  color: #ffffff;
  letter-spacing: 0.100em;
  padding-top: 30px; }

.banner-name h1 {
  font-size: 61px;
  letter-spacing: 0.100em;
  padding-bottom: 40px;
  font-weight: 600;
  text-transform: uppercase; }

.banner-name .btn {
  padding: 17px 47px;
  margin-top: 35px;
  font-weight: 600;
  background-color: #02101e;
  border: 3px solid #ffffff;
  letter-spacing: 0.100em; }

.left-image span {
  display: inline-block;
  width: 500px;
  height: 500px;
  border: 2px solid #000000;
  border-radius: 30px; }

.left-image span > img {
  border-radius: 30px; }

.right-image {
  padding-top: 90px;
  text-align: left;
  padding-right: 36px; }

.right-image h2 {
  font-size: 39px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.1em;
  padding-bottom: 19px; }

.right-image p {
  font-size: 20px;
  line-height: 30px; }

.left-image span img {
  max-width: 100%;
  max-height: 100%; }

.left-image {
  padding-top: 90px; }

.form-container {
  max-width: 528px;
  background-color: #010e1b;
  padding: 13px;
  border: 2px solid #000000;
  margin-left: -30px; }

.form-container .inner-text {
  color: #ffffff;
  font-size: 20px;
  text-align: left;
  padding-bottom: 19px; }

.form-container .inner-tedxt {
  font-size: 20px;
  color: #ffffff;
  font-size: 29px;
  text-align: left;
  padding-bottom: 30px; }

.form-container h2 {
  text-align: left;
  font-size: 40px;
  color: #ffffff;
  font-weight: 600;
  letter-spacing: 0.100em; }

.form-container .form-group {
  text-align: left; }

.form-container .form-group label {
  color: #ffffff;
  font-weight: 600;
  font-size: 17px; }

.form-container .form-group {
  max-width: 500px; }

.form-container .form-group ~ .btn {
  display: block;
  max-width: 200px;
  width: 100%;
  padding: 12px 10px;
  border-radius: 9px; }

@media only screen and (max-width: 767px) {
  .left-image span {
    width: 200px;
    height: 200px; }
  .form-container {
    margin-left: 0; }
  .right-image p {
    font-size: 14px;
    line-height: 24px; }
  .left-image {
    padding-top: 30px; }
  .right-image h2 {
    font-size: 31px;
    letter-spacing: 0.1em;
    padding: 20px 0 0; }
  .banner-name h1 {
    font-size: 30px; }
  .banner-name h1, .banner-name h2 {
    font-size: 31px;
    line-height: 37px; }
  .right-image {
    padding-top: 30px;
    text-align: left;
    padding: 20px; }
  .banner-name p {
    font-size: 15px;
    line-height: 30px; }
  .form-container h2 {
    text-align: left;
    font-size: 30px; } }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

